@import url(https://fonts.googleapis.com/css2?family=Lexend:wght@300;400;500;600;700&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap@5.3.0/dist/css/bootstrap.min.css);
/* Full-Screen Background with Darkened Corners */
.main-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background: 
    radial-gradient(circle, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.7) 100%),
    url(/fetch-dog-adoption/static/media/dog.2cf4c65a.jpg) center/cover no-repeat;
  text-align: center;
  position: relative;
  max-width: 100% !important;
  margin: 0%!important;
  padding: 0%!important;
}

/* Small Header: Fetch Dog Adoption */
.small-header {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 1.2rem;
  font-weight: bold;
  color: white;
  letter-spacing: 1px;
  text-transform: uppercase;
}

/* Adopt Me Button (Bottom-Center) */
.adopt-btn {
  position: absolute;
  bottom: 40px;
  left: 50%;
  font-size: 1.5rem;
  padding: 1rem 2.5rem;
  border-radius: 5px;
  background: transparent;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  border: 2px solid white;
}

.adopt-btn1 {
  padding: 10px 18px;
  border-radius: 5px;
  background: transparent;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  border: 2px solid white;
}
.modal-header {
  color: #fff;
  font-size: 30px;
  text-transform: uppercase;
  text-align: center !important;
}
.modal-subtitle{
  color: white;
  text-align: center;
}
/* Hover Effect */
.adopt-btn:hover,
.adopt-btn1:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.8);
}

.adopt-btn:active,
.adopt-btn1:active {
  background: rgba(255, 255, 255, 0.2);
}
/* Login Modal: Glassmorphism */
.modal-content {
  background: rgba(30, 30, 30, 0.8);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  padding: 2rem;
}

.modal-title {
  font-size: 2rem;
  font-weight: 600;
  color: white;
  text-align: center;
  margin-bottom: 1rem;
}

/* Glass-Effect Input Boxes */
.form-control {
  margin-bottom: 1rem;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  background: rgba(255, 255, 255, 0.1);
  color: white;
  padding: 1.2rem;
  transition: all 0.3s ease;
}

.form-control::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.form-control:focus {
  background: #ffffff26;
  border-color: #ffffff80;
  box-shadow: 0 0 10px #fff3;
  color: white;
}
/* Login Button */
.login-btn {
  width: 100%;
  padding: 1rem;
  font-size: 1.2rem;
  border-radius: 10px;
  background: linear-gradient(135deg, #6a11cb, #2575fc);
  color: white;
  border: none;
  font-weight: 600;
  transition: all 0.3s ease;
}

.login-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.login-btn:disabled {
  background: rgba(100, 100, 100, 0.5);
  cursor: not-allowed;
}
/* Dog Card Container */
.dog-card {
  background: rgba(30, 30, 30, 0.7);
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
  border-radius: 15px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  width: 100%;
  max-width: 300px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  margin: 10px;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.dog-card:hover {
  transform: translateY(-5px) scale(1.02);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.3);
}

/* Dog Image */
.dog-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-bottom: 3px solid #6a11cb;
}

/* Dog Info */
.dog-info {
  padding: 10px;
  background: rgba(40, 40, 40, 0.8);
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.dog-name {
  font-size: 18px;
  font-weight: 600;
  color: #ffffff;
  margin: 0;
}

.dog-breed {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 5px;
}

/* Dog Meta Information */
.dog-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.8);
}

.dog-zip,
.dog-age {
  display: flex;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
}

.dog-location {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.6);
  margin-top: 5px;
}

/* Favorite Button */
.favorite-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 10px;
  border-radius: 50%;
  transition: background-color 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.favorite-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.heart-icon {
  color: rgba(255, 255, 255, 0.6);
  font-size: 24px;
  transition: color 0.3s ease;
}

.heart-icon.filled {
  color: #ff4757;
}

/* Responsive Design */
@media (max-width: 768px) {
  .dog-card {
    max-width: 100%;
    margin: 10px 0;
  }

  .dog-image {
    height: 180px;
  }

  .dog-name {
    font-size: 16px;
  }

  .dog-breed {
    font-size: 13px;
  }

  .dog-meta {
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .dog-card {
    max-width: 100%;
    margin: 10px 0;
  }

  .dog-image {
    height: 150px;
  }

  .dog-name {
    font-size: 15px;
  }

  .dog-breed {
    font-size: 12px;
  }

  .dog-meta {
    font-size: 12px;
  }
}
